.friendList {
  margin: 0 auto;
  width: 300px;
}
.friendListItem {
  padding: 5px 0 5px 0;
  border-radius: 5px;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.2);
  margin-bottom: 5px;
}
.friendListItem:last-child {
  margin-bottom: 0;
}
.itemContainer {
  display: flex;
  align-items: center;
}
.avatar {
  margin-left: 20px;
  background-color: grey;
  border-radius: 5px;
}
.name {
  margin-left: 10px;
  font-weight: 700;
  font-size: 16px;
}
.status {
  margin-left: 10px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
}
